import * as React from 'react'
import Button from 'src/packages/button/Button'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import { renderString } from 'src/utils/renderCode'

class Start extends React.Component {
  public static displayName = 'Start'

  public render() {
    return (
      <div className="start">
        <section>
          <h1></h1>
          <p className="text">
            Xue-react 
            <a
              href="https://github.com/BlameDeng/xue-react"
              target="_blank"
              rel="noopener noreferrer"
              className="link"
            >
              
            </a>
            
          </p>
        </section>
        <section>
          <h2> Tslint</h2>
          <p className="text">
            Tslint
            Xue-react
             Tslint  tslint.json 
          </p>
        </section>
        <section>
          <h2> Scss</h2>
          <p className="text">
            Xue-react  Scss  Scss 
          </p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container">
            <Button style={{ marginRight: '20px' }} type="primary">
              Primary
            </Button>
          </div>
          <CodeBox
            title=" Button"
            description=" Xue-ui "
            defaultVisible={true}
          >
            {renderString(`import Button from 'components/Button'`)}
            <br />
            <Code
              code={`&lt;Button type="primary"&gt;Primary&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
      </div>
    )
  }
}

export default Start



